import { IS_SHOW_ADD_FORM, ADD_CLIENT } from 'actions/clients';
import { clientList, vacansyList } from 'data/data';

const initState = {
  isShowAddForm: false,
  clientList,
  vacansyList,
}

export default function(state = initState, action) {
  switch (action.type) {
    case IS_SHOW_ADD_FORM: {
      return {
        ...state,
        isShowAddForm: action.payload,
      }
    }
    case ADD_CLIENT: {
      return {
        ...state,
        clientList: [...state.clientList, action.payload],
      }
    }
    default:
  }
  return state;
}